@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}
html{
  background-color: black;
}
button::-moz-focus-inner {
  border: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Pink Slab';
  src: local('Pink Slab'), url(/static/media/PinkSlab-130.b06360f3.ttf) format('truetype');
}
@font-face {
  font-family: 'Barlow Semi Condensed';
  src: local('Barlow Semi Condensed'), url(/static/media/BarlowSemiCondensed-Regular.130d6a77.ttf) format('truetype');
}
@font-face {
  font-family: 'Barlow Semi Condensed Bold';
  src: local('Barlow Semi Condensed Bold'), url(/static/media/BarlowSemiCondensed-Bold.fc14de7d.ttf) format('truetype');
}
.veryfy-btn {
  margin-right: 3rem;
  font-weight: bold;
  color: white;
  cursor: pointer;
  border: solid 1px white;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 16px;
  padding-left: 16px;
  border-radius: 2px;
}
.veryfy-btn:hover {
  color: #f5b900;
  cursor: pointer;
  border: solid 1px #f5b900;
}

